<template>
  <div class="page-swap">
    <div class="from-wrap">
      <div class="from form-control">
        <div class="label">From <span>MAX</span></div>
        <div class="input" :class="{nounit: !from.coin}">
          <input type="text" placeholder="0.000" v-model="from.num" />
          <span class="unit" @click="selectCoin('from')">{{from.coin}} <i>▲</i></span>
        </div>
      </div>
      <div class="arrow" ><img @click="toggleFromTo" src="../../assets/exchange-arrow.png" /></div>
      <div class="to form-control">
        <div class="label">To (estimated)</div>
        <div class="input" :class="{nounit: !to.coin}">
          <input type="text" placeholder="0.000" v-model="to.num" />
          <span class="unit" @click="selectCoin('to')">选择代币</span>
        </div>
      </div>
    </div>
    <div class="act">
      <div class="act-btn">Connect Wallet</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "exchange-swap",
  data() {
    return {
      from: {
        num: "",
        coin: "eth",
      },
      to: {
        num: "",
        coin: "",
      },
    };
  },
  methods: {
    toggleFromTo() {
      const from = this.from
      this.from = this.to
      this.to = from
    },
    selectCoin(target) {
      const coin = this[target].coin
    }
  },
};
</script>
<style lang="less" scoped>
.page-swap {
  .from-wrap {
    margin-top: 15px;
    & > div {
      display: inline-block;
    }
    .arrow {
      width: 10%;
      // margin-left: 25px;
      // margin-right: 25px;
      vertical-align: top;
      // margin-top: 60px;
      text-align: center;
      height: 137px;
      line-height: 137px;
      img {
        width: 20px;
        width: 20px;
        vertical-align: middle;
        cursor: pointer;
      }
    }
    .form-control {
      width: 45%;
      padding: 24px;
      box-sizing: border-box;
      font-size: 22px;
      background: #fafafa;
      border-radius: 15px;
      .label {
        margin-bottom: 19px;
        span {
          float: right;
          color: #3b76fc;
        }
      }
      .input {
        white-space: nowrap;
        display: flex;
        input {
          border: none;
          outline: none;
          background: none;
          font-size: 38px;
          display: inline-block;
          color: #000;
          width:100%;
          padding: 0;
          &:-webkit-input-placeholder {
            color: #ccc;
          }
        }
        .unit {
          display: inline-block;
          width: auto;
          height: 45px;
          line-height: 45px;
          vertical-align: top;
          white-space: nowrap;
          text-align: right;
          cursor: pointer;
          text-transform: uppercase;
          border-radius: 9px;
          padding: 0 10px;
          box-sizing: border-box;
          &:hover {
            background: #ffd0254d;
          }
          i {
            transform: rotate(180deg);
            display: inline-block;
            font-size: 14px;
            height: 45px;
            line-height: 45px;
            vertical-align: bottom;
          }
        }
        &.nounit {
          .unit {
            background: #ffd0254d;
            color: #555;
          }
        }
      }
    }
    @media screen and (max-width: 760px) {
      .form-control {
        width: 100%;
      }
      .arrow {
        margin: 30px 0;
        width: 100%;
